*,
*:after,
*:before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  padding: 0;
  font-family: SrabenGrotesk, Arial, Helvetica, sans-serif;
  background-size: 100px;
  background-repeat: repeat;
  color: #395b76;
  background-color: #F8F7F3;
}

section {
  padding: 0 40px !important;
}

hr {
  max-width: 100px;
  margin: 50px auto;
  color: inherit;
  border: 0;
  border-top: 1px #395b76 solid;
  opacity: 0.25;
}

.sans-serif-text {
  font-family: Verona, Arial, Helvetica, sans-serif;
}

.frame-title, .day, .date, .year, .venue, .state {
  font-weight: 400;
}

.reveal {
  clip-path: inset(0 100% 0 0);
  transition: clip-path 0.5s;
  transition-timing-function: cubic-bezier(0.85, 0, 0.15, 1);
  overflow: hidden;
}

.animate-revealed {
  clip-path: inset(0);
}

a:hover,
a:focus {
  text-decoration: none;
  color: inherit;
}

b {
  font-weight: 600;
}

@media (min-width: 1400px) {
  .container {
    max-width: 1650px;
  }
}
a,
a[href^=tel],
a[href^=mailto] {
  color: inherit;
  /* Inherit text color of parent element. */
  text-decoration: none;
  /* Remove underline. */
}

.icon:before {
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  margin-right: 5px;
}

.main-content {
