  position: relative;
  background-color: rgba(253, 250, 245, 0.93);
}
.main-content .main-content-wrapper .main {
  position: relative;
}
.main-content .main-content-wrapper .main .box {
  position: absolute;
  background: white;
  border: 4px solid goldenrod;
}
.main-content .main-content-wrapper .main .box.box-top, .main-content .main-content-wrapper .main .box.box-bottom {
  width: 100%;
  height: 20px;
}
