  height: 100%;
  width: 20px;
}
.main-content .main-content-wrapper .main .box.box-top:before, .main-content .main-content-wrapper .main .box.box-top:after, .main-content .main-content-wrapper .main .box.box-bottom:before, .main-content .main-content-wrapper .main .box.box-bottom:after {
  background: white;
  height: 24px;
  position: absolute;
  width: 24px;
  border: 4px solid goldenrod;
}
.main-content .main-content-wrapper .main .box.box-top {
  top: -16px;
}
.main-content .main-content-wrapper .main .box.box-top:before {
  bottom: -24px;
  left: -4px;
}
.main-content .main-content-wrapper .main .box.box-top:after {
  right: -4px;
  bottom: -24px;
}
.main-content .main-content-wrapper .main .box.box-bottom {
  bottom: -16px;
}
.main-content .main-content-wrapper .main .box.box-bottom:before {
  left: -4px;
  top: -24px;
}
.main-content .main-content-wrapper .main .box.box-bottom:after {
  right: -4px;
  top: -24px;
}
.main-content .main-content-wrapper .main .box.box-left {
  left: -16px;
}
.main-content .main-content-wrapper .main .box.box-right {
  right: -16px;
}

